import { BODY } from "../../constants";
import encodeParams from "./encode_params";

export default function bodyMiddleware(options) {
  if (!BODY[options.method].request) {
    delete options.body;
    return options;
  }

  if (options.body) {
    return options;
  }

  if (options.params.constructor === Object) {
    options.body = encodeParams(options.params);
  } else {
    options.body = options.params;
  }

  if (!options.headers["content-type"] && options.body.constructor !== FormData) {
    options.headers["content-type"] = "application/x-www-form-urlencoded";
  }

  return options;
}
